.container {
  text-align: center;
  margin: 0 auto;
  overflow: auto;
  border: 0px solid black;
  width: 100%;
}

.col {
  width: 50%;
  float: left;
}

@media only screen and (max-width: 700px) {
  /* For mobile phones: */
  [class*="col"] {
    width: 100%;
  }
}



